@import url('https://fonts.googleapis.com/css2?family=Lexend:wght@100;200;300;400;500;600;700;800;900&display=swap');

*,
h1 {
    margin: 0;
    padding: 0;
}

@media screen and (min-width: 1080px) {
    .container {
        width: 90%;
        height: 70vh;
        margin: 0 auto;

        display: flex;
        align-items: center;
        justify-content: space-between;
    }
}